
@color-1: #f82b6d;
@color-2: #f5aa26;
@color-3: #762bf7;
@color-4: #44d9f8;
@color-5: #28bcf7;
@color-6: #f82b8f;


.new-index {
    width: 1090px;
    margin: 30px auto;
    .tabs {
        margin-bottom: 30px;
    }
    .Floor (@color) {
        .nav {
            position: relative;
        }
        
        .floor {
            position: absolute;
            bottom: 3px;
            left: 0;
        }
        .floor-title {
            position: absolute;
            bottom: 3px;
            left: 80px;
            margin-bottom: 0;
            font-size: 18px;
            font-family: microsoft yahei;
            line-height: 18px;

        }
        .nav-tabs>li {
            float: right;
        }
        .nav>li>a {
            padding: 5px 14px;
        }

        .nav>li>a:hover {
            background-color: white;
            border: 1px solid white;
            border-bottom: 0;
            color: @color;
        }
        .nav-tabs>li>a {
            border-radius: 0;
            margin-right: 0;
            font-size: 12px;
            color: #6D6D6D;
        }

        .tab-pane:after {
            content: "."; 
            display: block;
            height: 0; 
            clear: both; 
            visibility: hidden;
        }

        .nav-tabs  {
            border-bottom: 1px solid @color;

        }

        .nav-tabs>li.active>a {
            border: 1px solid @color;
            border-bottom-color: transparent;
            color: @color;
        }
        .left-menu {
            width: 184px;
            position: relative;
            float: left;
            img {
                width: 184px;
                height: auto;
            }
            .menu-select {
                width: 184px;
                position: absolute;
                left: 0;
                top: 0;
                z-index: 2;
                .select-list {
                    width: 50%;
                    float: left;
                    a {
                        display: block;
                        float: left;
                        margin: 15px 0 0 15px;
                        padding: 3px 5px;
                        font-size: 12px;
                        border-radius: 3px;
                        color: #6D6D6D;
                        text-decoration: none;
                    }
                    .select-list-r {
                        margin: 15px 0 0 0;
                    }
                    a:hover {
                        color: @color;
                    }
                    .active {
                        background-color: @color;
                        color: white;
                    }
                    .active:hover {
                        color: white;
                    }
                }
            }
        }

        .right-pro {
            width: 906px;
            /*height: 484px;*/
            float: right;
            border-right: 1px solid #ccc;
            .slide {
                width: 362px;
                height: auto;
                float: left;
            }
            .sm-box {
                width: 181px;
                float: left;
                border-left: 1px solid #ccc;
                border-bottom: 1px solid #ccc;
                text-align: center;
                img {
                    width: 180px;
                    height: auto;
                }
                .pro-info {
                    width: 180px;
                    height: 51px;
                    .pro-info-name {
                        float: left;
                        height: 12px;
                        overflow: hidden;
                        padding: 0 8px;
                        margin-top: 5px;
                        a {
                            font-size: 12px;
                            line-height: 12px;
                            color: #999;
                            height: 12px;
                            margin-bottom: 0;
                            text-decoration: none;
                            display: inherit;

                        }
                        a:hover {
                            color: @color-1;
                        }
                    }
                    .pro-info-price {
                        float: left;
                        width: 100%;
                        p {
                            text-align: center;
                            margin-bottom: 0;
                            font-size: 18px;
                            color: @color-1;
                            line-height: 18px;
                            margin-top: 10px;
                        }
                    }
                }
            }
        }
    }

    .first {
        .Floor(@color-1);
    }
    .second {
        .Floor(@color-5);
    }
    .third {
        .Floor(@color-3);
    }
    .fourth {
        .Floor(@color-6);
    }
    .fifth {
        .Floor(@color-2);
    }
    .sixth {
        .Floor(@color-4);
    }

}
